*, :before, :after {
  box-sizing: border-box;
}

body {
  color: #2a2a2a;
  font-family: Helvetica Neue, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, sans-serif;
  line-height: 1.5;
}

button, label {
  cursor: pointer;
}

input {
  font: inherit;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.feedback-form {
  width: 480px;
}

.feedback-form > label {
  width: 100%;
  flex-direction: column;
  margin-bottom: 16px;
  padding: 8px;
  display: flex;
}

.feedback-form input, .feedback-form textarea {
  font: inherit;
  padding: 8px;
}

.feedback-form textarea {
  resize: none;
}

body {
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

.palette {
  max-width: 1200px;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  list-style: none;
  display: grid;
}

.color-card {
  background-color: #fff;
  border-radius: 4px;
  padding: 5px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  box-shadow: 0 2px 1px -1px #0003, 0 1px 1px #00000024, 0 1px 3px #0000001f;
}

.color-card.is-active {
  transform: perspective(400px)rotateX(10deg);
}

.color-swatch {
  height: 128px;
  cursor: pointer;
}

.color-meta {
  padding: 10px;
  font-weight: 700;
}

.color-meta p {
  letter-spacing: .1em;
  margin-top: 0;
  margin-bottom: 0;
}

.gallery {
  max-width: calc(100% - 64px);
  grid-gap: 24px;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  justify-content: center;
  margin: 0 auto;
  padding: 0;
  list-style: none;
  display: grid;
}

.gallery__item {
  padding: 30px;
}

.gallery__item p {
  margin-top: 0;
  margin-bottom: 8px;
}

.gallery__thumb {
  height: 200px;
  margin-bottom: 16px;
}

.gallery__item img {
  object-fit: cover;
  max-width: 100%;
  height: 100%;
}

